// rootReducer.ts

import {
    WS_CONNECTION_SUCCESS,
    WS_CONNECTION_ERROR,
    WS_CONNECTION_CLOSED,
    WS_GET_MESSAGE
  } from '../constants/websocket';
  import type { TApplicationActions } from '../types';
  
  type TWSState = {
    wsConnected: boolean;
    messages: string[];
  
    error?: Event;
  }
  
  export const initialState: TWSState = {
    wsConnected: false,
    messages: [],
    error: undefined
  };
  
  //    WebSocket
  export const wsReducer = (state = initialState, action: TApplicationActions) => {
    switch (action.type) {
          //      WS_CONNECTION_SUCCESS
          //   wsConnected   true
      case WS_CONNECTION_SUCCESS:
        return {
          ...state,
          error: undefined,
          wsConnected: true
        };
  
          //      WS_CONNECTION_ERROR
          //   wsConnected   false     action.payload
      case WS_CONNECTION_ERROR:
        return {
          ...state,
          error: action.payload,
          wsConnected: false
        };
  
          //      WS_CONNECTION_CLOSED,   
          //   wsConnected   false
      case WS_CONNECTION_CLOSED:
        return {
          ...state,
          error: undefined,
          wsConnected: false
        };
  
          //      WS_GET_MESSAGE
          //  ,     
          //  messages  ,    
      case WS_GET_MESSAGE:
        return {
          ...state,
          error: undefined,
          messages: [...state.messages, action.payload]
        };
      default:
        return state;
    }
  };